import { 
  createRouter,
  createWebHashHistory,
} from "vue-router";
import {
  routeList
} from "./route-list";
import {
  getAssetConfig,
  setAssetConfigRecord
} from "@/utils/config-helper";
import {
  loadAllConfig
} from "@/api/axios-helper";
import { isShowAppLoading } from "@/utils/app-helper";

const router = createRouter({
  history: createWebHashHistory(),
  routes: routeList,
});

router.beforeEach((to, from, next) => {
  const assetConfig = getAssetConfig();
  if (assetConfig) {
    next();
    return;
  }
  isShowAppLoading.value = true;
  loadAllConfig().then((obj) => {
    setAssetConfigRecord(obj);
    isShowAppLoading.value = false;
    next();
  })
});

export default router;
