import { 
  type LangId,
  getCurrentLangId
} from "./lang-helper"

export interface HomeViewCard {
  title: string,
}

export interface HomeViewCircle {
  title: string,
  description: string,
  email: string,
}

export interface HomeViewImgSrc {
  logo: string,
  pencil: string,
}

export interface AssetConfigHomeView {
  imgSrc: HomeViewImgSrc,
  title: string,
  description: string,
  cardList: Array<HomeViewCard>,
  circleInfo: HomeViewCircle,
}

export interface DetailViewImgSrc {
  logo: string,
}

export interface AssetConfigDetailView {
  banner: string[],
  title: string,
  description: string,
  imgList: string[],
  articleImgList: string[],
  imgSrc: DetailViewImgSrc,
}

export interface AssetConfig {
  /**
   * @description 首页
   */
  homeView: AssetConfigHomeView,
  detailView: AssetConfigDetailView,
}

export type AssetConfigRecord = Record<LangId, AssetConfig>;

let assetConfigRecord = null as null | AssetConfigRecord;

export function setAssetConfigRecord(record: AssetConfigRecord) {
  assetConfigRecord = record;
}

export function getAssetConfig(): AssetConfig | null {
  if (assetConfigRecord) {
    const langId = getCurrentLangId();
    return assetConfigRecord[langId] || null;
  }
  return null;
}

export function getAssetConfigSure(): AssetConfig {
  return getAssetConfig()!;
}
