import { 
  type LangId,
  langList
 } from "@/utils/lang-helper";
import * as NSAxios from "axios";
import {
  type AssetConfig,
} from "../utils/config-helper";


const apiHelper = NSAxios.default.create({  
});

export {
  apiHelper,
};

export function loadConfig(lang: LangId): Promise<AssetConfig> {
  return apiHelper.get(`/config-assets/text/${lang}.json`).then((res) => {
    return res.data;
  });
}

export function loadAllConfig(): Promise<Record<LangId, AssetConfig>> {
  return Promise.all(
    langList.map((langId) => {
      return loadConfig(langId.id);
    })
  ).then((configList) => {
    const obj = {} as Record<LangId, AssetConfig>;
    configList.forEach((configItem, configIndex) => {
      obj[langList[configIndex].id] = configItem;
    });
    return obj;
  });
}
